header.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 40px;
  max-width: 1300px; }
  header.main-header nav .logo {
    height: 50px; }
  header.main-header nav a {
    padding: 20px;
    font-size: 24px;
    opacity: 0.5;
    transition: 0.25s;
    position: relative; }
    header.main-header nav a:after {
      content: "";
      position: absolute;
      left: 10px;
      right: 10px;
      bottom: 20px;
      height: 0;
      z-index: -1;
      background: var(--highlight);
      transition: 0.25s; }
    header.main-header nav a:hover, header.main-header nav a.selected {
      opacity: 0.99; }
      header.main-header nav a:hover:after, header.main-header nav a.selected:after {
        height: 10px; }
